.categories form{
  background: #f7f7f7;
  padding: 2rem;
  margin-bottom: 2rem;
  border-radius: .5rem;
  display: flex;
  justify-content: space-evenly;
}

.categories form .upload{
  max-width: 150px;
    height: 150px;
    width: 100%;
    border: 1px solid #130f40;
    padding: 15px;
    margin: 20px;
    position: relative;
}

.categories form #file_up{
  position: relative;
  width: 100%;
  height: 100%;
  outline: none;
}

.categories form #file_up::before{
  content: '+';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: white;
  color: #27ae60;
  font-size: 10rem;
  text-align: center;
  cursor: pointer;
  margin: auto;
}

.categories form #file_img{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: white;
}

.categories form #file_img img{
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
}

.categories form #file_img span{
  position: absolute;
  top: -13px;
  right: -13px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 50%;
  padding: 6px 10px;
  cursor: pointer;
  font-weight: 900;
  color: crimson;
}

.categories form .inputBox{
  padding: .7rem 0;
