/* ------------------------- Delete All ------------------------- */

.delete-all {
    text-align: right;
    margin: 20px;
  }
  
  .delete-all input {
    height: 20px;
    width: 20px;
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    margin: 0 15px;
  }
  
  .delete-all span {
    text-transform: uppercase;
